
cc.Class({
    extends: cc.Component,

    properties: {
        time:0,
    },
    on_btn_onclick:function(event, customEventData){
    	if (this.time <= 53 && this.time >=48) {
    		if(customEventData == "C"){
    			let tureNode = cc.find("Canvas/bg_2/truenode");
    			tureNode.active = true;
    			this.scoreNum ++;
    			let questionNode = cc.find("Canvas/bg_2/qusetion");
    			questionNode.active = false;
    			let N1Node = cc.find("Canvas/bg_2/NPC_1");
    			N1Node.active = false;
    		} else {
    			let falseNode = cc.find("Canvas/bg_2/falsenode");
    			falseNode.active = true;
    			let questionNode = cc.find("Canvas/bg_2/qusetion");
    			questionNode.active = false;
    		}
    	}
    	if (this.time <= 45 && this.time >=40) {
    		if(customEventData == "A"){
    			let tureNode = cc.find("Canvas/bg_2/truenode");
    			tureNode.active = true;
    			this.scoreNum ++;
    			let questionNode = cc.find("Canvas/bg_2/qusetion");
    			questionNode.active = false;
    			let N3Node = cc.find("Canvas/bg_2/NPC_3");
    			N3Node.active = false;
    		} else {
    			let falseNode = cc.find("Canvas/bg_2/falsenode");
    			falseNode.active = true;
    			let questionNode = cc.find("Canvas/bg_2/qusetion");
    			questionNode.active = false;
    			
    		}
    	}
        if (this.time <= 37 && this.time >=35) {
    		if(customEventData == "C"){
    			let tureNode = cc.find("Canvas/bg_2/truenode");
    			tureNode.active = true;
    			this.scoreNum ++;
    			let questionNode = cc.find("Canvas/bg_2/qusetion");
    			questionNode.active = false;
    			let N4Node = cc.find("Canvas/bg_2/NPC_4");
    			N4Node.active = false;
    		} else {
    			let falseNode = cc.find("Canvas/bg_2/falsenode");
    			falseNode.active = true;
    			let questionNode = cc.find("Canvas/bg_2/qusetion");
    			questionNode.active = false;
    		}
    	}	
    	if (this.time <= 28 && this.time >=23) {
    		if(customEventData == "C"){
    			let tureNode = cc.find("Canvas/bg_2/truenode");
    			tureNode.active = true;
    			this.scoreNum ++;
    			let questionNode = cc.find("Canvas/bg_2/qusetion");
    			questionNode.active = false;
    			let N2Node = cc.find("Canvas/bg_2/NPC_2");
    			N2Node.active = false;
    		} else {
    			let falseNode = cc.find("Canvas/bg_2/falsenode");
    			falseNode.active = true;
    			let questionNode = cc.find("Canvas/bg_2/qusetion");
    			questionNode.active = false;
    		}
    	}	
    	if (this.time <= 20 && this.time >=15) {
    		if(customEventData == "A"){
    			let tureNode = cc.find("Canvas/bg_2/truenode");
    			tureNode.active = true;
    			this.scoreNum ++;
    			let questionNode = cc.find("Canvas/bg_2/qusetion");
    			questionNode.active = false;
    		} else {
    			let falseNode = cc.find("Canvas/bg_2/falsenode");
    			falseNode.active = true;
    			let questionNode = cc.find("Canvas/bg_2/qusetion");
    			questionNode.active = false;
    		}
    	}
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad () {
    	this.scoreNum = 0;
    	
    },

    start () {
    	let countDownLabel = cc.find("Canvas/Time").getComponent(cc.Label);					
    	let scoreLabel = cc.find("Canvas/bg_2/score").getComponent(cc.Label);
    	let questionLabel = cc.find("Canvas/bg_2/qusetion/labelq").getComponent(cc.Label);
    	let aLabel = cc.find("Canvas/bg_2/qusetion/labela").getComponent(cc.Label);
    	let bLabel = cc.find("Canvas/bg_2/qusetion/labelb").getComponent(cc.Label);
    	let cLabel = cc.find("Canvas/bg_2/qusetion/labelc").getComponent(cc.Label);
    	let questionNode = cc.find("Canvas/bg_2/qusetion");
    	let falseNode = cc.find("Canvas/bg_2/falsenode");
    	let tureNode = cc.find("Canvas/bg_2/truenode");
    	let playerNode = cc.find("Canvas/bg_2/player");
    	//挂载题目和选项
    	questionLabel.string = "";
    	aLabel.string = "";
    	bLabel.string = "";
    	cLabel.string = "";
    	//初始化题目和选项
    	scoreLabel.string = "Score:" + this.scoreNum;
    	countDownLabel.string = this.time + "s";
    	//初始化分数和时间
    	this.schedule(function(){
    		this.time --;
    		countDownLabel.string = this.time + "s";
    		scoreLabel.string = "Score:" + this.scoreNum;
            if (this.time <= 53 && this.time >=48){
            	questionLabel.string = "1.Which of the following words has the same meaning as the word ‘multiple’ here?";
            	aLabel.string = "A.14,21and28 are all multiples of 7";
    			bLabel.string = "B.It made it almost impossible for the smaller retailer to compete against the multiples.";
    			cLabel.string = "C.He suffered multiple injuries.";
            	if (this.time == 53){
            		questionNode.active = true;
            	
            	}
            	if (this.time == 48){
            		questionNode.active = false;
            	}
            }
            //第一题
            if (this.time <= 45 && this.time >=40){
            	
            	questionLabel.string = "2.What does the word ‘eventually’ mean here?";
            	aLabel.string = "A.after a long period of time or an especially long delay;";
    			bLabel.string = "B.to an indefinite extent; for an indefinite time";
    			cLabel.string = "C.for a limitless time;";
            	if (this.time == 45){
            		questionNode.active = true;
            		tureNode.active = false;
    				falseNode.active = false;
            	
            	}
            	if (this.time == 40){
            		questionNode.active = false;
            	}
            }
            //第二题
            if (this.time <= 37 && this.time >=32){
            	questionLabel.string = "3.Which of the following words ‘originated’ has the same meaning as it here?";
            	aLabel.string = "A.Locke originated this theory in the 17th century.";
    			bLabel.string = "B.The foundation of western constitutionalism originates upon the classical liberalism.";
    			cLabel.string = "C.The disease is thought to have originated in the tropics.";
            	if (this.time == 37){
            		questionNode.active = true;
            		tureNode.active = false;
    				falseNode.active = false;
            	
            	}
            	if (this.time == 32){
            		questionNode.active = false;
            	}
            }
            //第三题
            if (this.time <= 28 && this.time >=23){
            	questionLabel.string = "4.Which of the following routes is true for the Silk Road?";
            	aLabel.string = "A.Luoyang——Tianshan——Lanzhou——Tarim Basin";
    			bLabel.string = "B.Luoyang——Tarim Basin——Tianshan——Dunhuang";
    			cLabel.string = "C.Luoyang——Lanzhou——Dunhuang ——Tianshan";
            	if (this.time == 28){
            		questionNode.active = true;
            		tureNode.active = false;
    				falseNode.active = false;
            	
            	}
            	if (this.time == 23){
            		questionNode.active = false;
            	}
            }
            //第四题
            if (this.time <= 20 && this.time >=15){
            	questionLabel.string = "5.Where was the route split into three ways?";
            	aLabel.string = "A.Dunhuang";
    			bLabel.string = "B.Lanzhou";
    			cLabel.string = "C.Tianshan ;";
            	if (this.time == 20){
            		questionNode.active = true;
            		tureNode.active = false;
    				falseNode.active = false;
            	
            	}
            	if (this.time == 15){
            		questionNode.active = false;
            	}
            }
			if (this.time == 12){
				playerNode.active = false;
				tureNode.active = false;
    			falseNode.active = false;
    			let finalNode = cc.find("Canvas/bg_2/final");
    			finalNode.active = true;
    			let endNode = finalNode.getChildByName("end");
    			endNode.getComponent(cc.Label).string = "Score: " + this.scoreNum;
    			let textNode = finalNode.getChildByName("text");
    			let textLabel = textNode.getComponent(cc.Label);
    			switch (true) {
                    case this.scoreNum <= 3:
                        textLabel.string = "TRY AGAIN";	
                        break;
                    case this.scoreNum < 5:
                        textLabel.string = "NOT BAD";
                        break;
                    case this.scoreNum == 5:
                        textLabel.string = "GOOD";
                        break;

                }
			}
			if(this.time == 8){
				
				if(this.scoreNum >= 4) {
					cc.director.loadScene("main_2");
				} else {
					cc.director.loadScene("main_1");
				}
				
			}
    	},1);

    },

    // update (dt) {},
});
