const player_skin = cc.Class({
    name:"player_skin",
    properties: {
        player:{
            default:[],
            type:[cc.SpriteFrame]
        }
    }
});
cc.Class({
    extends: cc.Component,

    properties: {
        player_sets:{
            default: [],
            type: [player_skin]
        }
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad () {
    	this.intervalTime = 0;
    },

    start () {

    },

    update (dt) {
        // 间隔时间
        this.intervalTime += dt;
        let index = Math.floor(this.intervalTime / 0.2);
        index = index%3;
        // 获取一种类型
        let playerSet = this.player_sets[0];

        // 获取精灵组件
        let sprite = this.node.getComponent(cc.Sprite);
        sprite.spriteFrame = playerSet.player[index];
    },
//  runCallback() {
//      cc.log("一个轮回结束！");
//      this.randomType = Math.floor(Math.random()*3);
//  }
});
