
cc.Class({
    extends: cc.Component,

    properties: {
        time:0,
    },
    on_btn_onclick:function(event, customEventData){
    	if (this.time <= 53 && this.time >=48) {
    		if(customEventData == "A"){
    			let tureNode = cc.find("Canvas/bg_2/truenode");
    			tureNode.active = true;
    			this.scoreNum ++;
    			let questionNode = cc.find("Canvas/bg_2/qusetion");
    			questionNode.active = false;
    			let N1Node = cc.find("Canvas/bg_2/NPC_1");
    			N1Node.active = false;
    		} else {
    			let falseNode = cc.find("Canvas/bg_2/falsenode");
    			falseNode.active = true;
    			let questionNode = cc.find("Canvas/bg_2/qusetion");
    			questionNode.active = false;
    		}
    	}
    	if (this.time <= 45 && this.time >=40) {
    		if(customEventData == "C"){
    			let tureNode = cc.find("Canvas/bg_2/truenode");
    			tureNode.active = true;
    			this.scoreNum ++;
    			let questionNode = cc.find("Canvas/bg_2/qusetion");
    			questionNode.active = false;
    			let N2Node = cc.find("Canvas/bg_2/NPC_2");
    			N2Node.active = false;
    		} else {
    			let falseNode = cc.find("Canvas/bg_2/falsenode");
    			falseNode.active = true;
    			let questionNode = cc.find("Canvas/bg_2/qusetion");
    			questionNode.active = false;
    		}
    	}
        if (this.time <= 37 && this.time >=35) {
    		if(customEventData == "C"){
    			let tureNode = cc.find("Canvas/bg_2/truenode");
    			tureNode.active = true;
    			this.scoreNum ++;
    			let questionNode = cc.find("Canvas/bg_2/qusetion");
    			questionNode.active = false;
    			let N3Node = cc.find("Canvas/bg_2/NPC_3");
    			N3Node.active = false;
    		} else {
    			let falseNode = cc.find("Canvas/bg_2/falsenode");
    			falseNode.active = true;
    			let questionNode = cc.find("Canvas/bg_2/qusetion");
    			questionNode.active = false;
    		}
    	}	
    	if (this.time <= 28 && this.time >=23) {
    		if(customEventData == "B"){
    			let tureNode = cc.find("Canvas/bg_2/truenode");
    			tureNode.active = true;
    			this.scoreNum ++;
    			let questionNode = cc.find("Canvas/bg_2/qusetion");
    			questionNode.active = false;
    			let N4Node = cc.find("Canvas/bg_2/NPC_4");
    			N4Node.active = false;
    		} else {
    			let falseNode = cc.find("Canvas/bg_2/falsenode");
    			falseNode.active = true;
    			let questionNode = cc.find("Canvas/bg_2/qusetion");
    			questionNode.active = false;
    		}
    	}	
    	if (this.time <= 20 && this.time >=15) {
    		if(customEventData == "B"){
    			let tureNode = cc.find("Canvas/bg_2/truenode");
    			tureNode.active = true;
    			this.scoreNum ++;
    			let questionNode = cc.find("Canvas/bg_2/qusetion");
    			questionNode.active = false;
    		} else {
    			let falseNode = cc.find("Canvas/bg_2/falsenode");
    			falseNode.active = true;
    			let questionNode = cc.find("Canvas/bg_2/qusetion");
    			questionNode.active = false;
    		}
    	}
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad () {
    	this.scoreNum = 0;
    	
    },

    start () {
    	let countDownLabel = cc.find("Canvas/Time").getComponent(cc.Label);					
    	let scoreLabel = cc.find("Canvas/bg_2/score").getComponent(cc.Label);
    	let questionLabel = cc.find("Canvas/bg_2/qusetion/labelq").getComponent(cc.Label);
    	let aLabel = cc.find("Canvas/bg_2/qusetion/labela").getComponent(cc.Label);
    	let bLabel = cc.find("Canvas/bg_2/qusetion/labelb").getComponent(cc.Label);
    	let cLabel = cc.find("Canvas/bg_2/qusetion/labelc").getComponent(cc.Label);
    	let questionNode = cc.find("Canvas/bg_2/qusetion");
    	let falseNode = cc.find("Canvas/bg_2/falsenode");
    	let tureNode = cc.find("Canvas/bg_2/truenode");
    	let playerNode = cc.find("Canvas/bg_2/player");
    	//挂载题目和选项
    	questionLabel.string = "";
    	aLabel.string = "";
    	bLabel.string = "";
    	cLabel.string = "";
    	//初始化题目和选项
    	scoreLabel.string = "Score:" + this.scoreNum;
    	countDownLabel.string = this.time + "s";
    	//初始化分数和时间
    	this.schedule(function(){
    		this.time --;
    		countDownLabel.string = this.time + "s";
    		scoreLabel.string = "Score:" + this.scoreNum;
            if (this.time <= 53 && this.time >=48){
            	questionLabel.string = "1.What is the meaning of the word ‘notable’?";
            	aLabel.string = "A.widely known and esteemed";
    			bLabel.string = "B.having an exceedingly bad reputation";
    			cLabel.string = "C.of major significance or importance";
            	if (this.time == 53){
            		questionNode.active = true;
            	
            	}
            	if (this.time == 48){
            		questionNode.active = false;
            	}
            }
            //第一题
            if (this.time <= 45 && this.time >=40){
            	
            	questionLabel.string = "2.Xi’an is the starting point of ________";
            	aLabel.string = "A.	Maritime Silk Road";
    			bLabel.string = "B.	Digital Silk Road";
    			cLabel.string = "C.	ancient Silk Roade";
            	if (this.time == 45){
            		questionNode.active = true;
            		tureNode.active = false;
    				falseNode.active = false;
            	
            	}
            	if (this.time == 40){
            		questionNode.active = false;
            	}
            }
            //第二题
            if (this.time <= 37 && this.time >=32){
            	questionLabel.string = "3.	The word ‘prosperous’ here means_______";
            	aLabel.string = "A.tending to favor or bring good luck;";
    			bLabel.string = "B.in fortunate circumstances";
    			cLabel.string = "C.marked by peace and prosperity";
            	if (this.time == 37){
            		questionNode.active = true;
            		tureNode.active = false;
    				falseNode.active = false;
            	
            	}
            	if (this.time == 32){
            		questionNode.active = false;
            	}
            }
            //第三题
            if (this.time <= 28 && this.time >=23){
            	questionLabel.string = "4.What is the correct expression of ‘兵马俑’ in English?";
            	aLabel.string = "A.	Stone soldiers and horses;";
    			bLabel.string = "B.	Terracotta Warriors";
    			cLabel.string = "C.	Terracotta Soldiers;";
            	if (this.time == 28){
            		questionNode.active = true;
            		tureNode.active = false;
    				falseNode.active = false;
            	
            	}
            	if (this.time == 23){
            		questionNode.active = false;
            	}
            }
            //第四题
            if (this.time <= 20 && this.time >=15){
            	questionLabel.string = "5.Which of the following words can be used to replace the word ‘undying’ here?";
            	aLabel.string = "A.	continuous;";
    			bLabel.string = "B.	eternal";
    			cLabel.string = "C.	expectable;";
            	if (this.time == 20){
            		questionNode.active = true;
            		tureNode.active = false;
    				falseNode.active = false;
            	
            	}
            	if (this.time == 15){
            		questionNode.active = false;
            	}
            }
			if (this.time == 12){
				playerNode.active = false;
				tureNode.active = false;
    			falseNode.active = false;
    			let finalNode = cc.find("Canvas/bg_2/final");
    			finalNode.active = true;
    			let endNode = finalNode.getChildByName("end");
    			endNode.getComponent(cc.Label).string = "Score: " + this.scoreNum;
    			let textNode = finalNode.getChildByName("text");
    			let textLabel = textNode.getComponent(cc.Label);
    			switch (true) {
                    case this.scoreNum <= 3:
                        textLabel.string = "TRY AGAIN";	
                        break;
                    case this.scoreNum < 5:
                        textLabel.string = "NOT BAD";
                        break;
                    case this.scoreNum == 5:
                        textLabel.string = "GOOD";
                        break;

                }
			}
			if(this.time == 8){
				
				if(this.scoreNum >= 4) {
					cc.director.loadScene("main_3");
				} else {
					cc.director.loadScene("main_2");
				}
				
			}
    	},1);

    },

    // update (dt) {},
});
